import { Center, Grid, Box, Text, Image, GridItem } from "@chakra-ui/react";
import React from "react";

let skillsData = [
  {
    image:
      "https://cdn.jsdelivr.net/gh/devicons/devicon/icons/html5/html5-original.svg",
    name: "HTML",
  },
  {
    image:
      "https://cdn.jsdelivr.net/gh/devicons/devicon/icons/css3/css3-plain.svg",
    name: "CSS",
  },
  {
    image:
      "https://cdn.jsdelivr.net/gh/devicons/devicon/icons/javascript/javascript-original.svg",
    name: "JavaScript",
  },
  {
    image:
      "https://raw.githubusercontent.com/devicons/devicon/master/icons/react/react-original-wordmark.svg",
    name: "React",
  },
  {
    image:
      "https://dgdeepon.github.io/static/media/chakra-ui_title2-400x400-removebg-preview.fc0f93596462cd01a157.png",
    name: "Chakra UI",
  },
  {
    image:
      "https://raw.githubusercontent.com/devicons/devicon/master/icons/redux/redux-original.svg",
    name: "Redux",
  },
  {
    image:
      "https://raw.githubusercontent.com/devicons/devicon/master/icons/typescript/typescript-original.svg",
    name: "TypeScript",
  },
  {
    image:
      "https://cdn.jsdelivr.net/gh/devicons/devicon/icons/mongodb/mongodb-plain.svg",
    name: "Mongo DB",
  },
  {
    image:
      "https://www.pixelbird.com.au/wp-content/uploads/2020/02/nodejs-image.png",
    name: "Node.js",
  },
  {
    image:
      "https://raw.githubusercontent.com/devicons/devicon/master/icons/express/express-original-wordmark.svg",
    name: "Express",
  },
];

const Skills = () => {
  return (
    <Box py={["0px", "60px", "80px"]} id="skills">
      <Center fontSize={40} color="yellow" position="relative" textAlign="center">
        <Text fontWeight={600}>SKILLS</Text>
      </Center>
      <br />
      <br />
      <Center position="relative">
        <Grid
          p={["4%", "0%", "0%", "0%"]}
          templateColumns={{
            base: "repeat(2,1fr)",
            sm: "repeat(3,1fr)",
            md: "repeat(5,1fr)",
          }}
          gap={5}
        >
          {skillsData?.map((ele) => {
            return (
              <GridItem
                width={{ base: "100px", sm: "150px", md: "190px" }}
                p={4}
                border="0.5px solid #0A192F"
                className="skills-card"
                padding={"35px"}
                overflow="hidden"
                style={{
                  boxShadow: "rgba(100, 100, 111, 0.2) 0px 7px 29px 0px",
                }}
                color="white"
                
              >
                <Image
                  w="100%"
                  transition="all .1s ease-in-out"
                  _hover={{
                    transform: "scale(1.1)",
                  }}
                  borderRadius="10%"
                  src={ele.image}
                  width={{ base: "100px", sm: "150px", md: "190px" }}
                  className="skills-card-img"
                  // height="100px"
                />
                <Text fontSize="24px" color="white" className="skills-card-name">
                  {" "}
                  {ele.name}
                </Text>
              </GridItem>
            );
          })}
        </Grid>
      </Center>
    </Box>
  );
};

export default Skills;
